import React from 'react';
import { Grid, Cell } from 'react-mdl';

function projects() {
  return (
    <div className="projectWrapper" style={{ width: "100%", margin: "auto" }}>
      <Grid className="project-grid">
          <Cell col={12}>
              <h1>Projects here</h1>
          </Cell>

      </Grid>
    </div>
  );
}

export default projects;